.wrap {
    display: flex;
    box-sizing: border-box;
    flex-direction: column;
    width: 100%;
    gap: 20px; 
    background: #fff;
    border-radius: 27px;
    padding: 20px;
    box-sizing: border-box;
    width: 100%;
font-size: 14px;
line-height: 129%;
color: #6a6a6f;
}

.wrapTab {
    display: flex;
    gap: 8px;
}

.tabDescription, .tabReviews {
    border-radius: 40px;
padding: 8px 0;
background: rgba(89, 177, 122, 0.1);
height: 33px;
font-weight: 500;
font-size: 14px;
letter-spacing: -0.05em;
text-align: center;
color: #59b17a;
border: none;
}

.tabReviews {
    width: 102px;  
}
.tabDescription {
    width: 120px;
}

.tabDescription:hover, .tabReviews:hover {
    color: #fff;
    background: #59b17a;
}
.tabDescription:active, .tabReviews:active, .activeTab {
    color: #fff;
    background: #59b17a;
}